import React from "react";
import parse from "html-react-parser";

import { questionToAnswers } from "../QuestionAndAnswers";

import "../CSS/Java.css";

function Java() {
  let counter = 0;
  let id = "a";
  return (
    <div class="accordion" id="accordionExample">
      {Object.keys(questionToAnswers).map((key) => (
        <div class="card">
          <div class="card-header" id="headingTwo">
            <h2 class="mb-0">
              <button
                class="btn btn-link btn-block text-left collapsed text-dark text-monospace text-bold text-decoration-none font-weight-bold"
                type="button"
                data-toggle="collapse"
                data-target={"#" + id}
              >
                {++counter + "). " + key}
              </button>
            </h2>
          </div>
          <div
            id={id}
            class="collapse"
            aria-labelledby="headingTwo"
            data-parent="#accordionExample"
          >
            <div class="card-body text-monospace">
              <p className="font-weight-bold answer ">Answer:</p>
              {parse(questionToAnswers[key])}
            </div>
          </div>
        </div>
      ))}
    </div>
  );
}

export default Java;
