import React from "react";

import logo from "../Logos/binary-code.png";

import "../App.css";
import "../CSS/Nav.css";
import { Link } from "react-router-dom";

function Nav() {
  return (
    <div className="custom-navbar">
      <nav class="navbar navbar-expand-lg navbar-light bg-white shadow-sm">
        <a class="navbar-brand" href="#">
          <img src={logo} height="50px" width="50px" /> Skill Bite
        </a>

        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>

        <div class="collapse navbar-collapse " id="navbarSupportedContent">
          <ul class="navbar-nav d-flex justify-content-around nav-fill w-100">
            <li class="nav-item">
              <a class="nav-link" href="/home">
                Home
              </a>
              {/* <Link to="/">Home</Link> */}
            </li>
            <li class="nav-item ">
              <a class="nav-link" href="/">
                Java
              </a>
              {/* <Link to="/">Java</Link> */}
            </li>
            <li class="nav-item">
              <a class="nav-link" href="/operatingsystems">
                Operating Systems
              </a>
              {/* <Link to="/">Operating Systems</Link> */}
            </li>
            <li class="nav-item">
              <a class="nav-link" href="/computernetworks">
                Computer Networks
              </a>
              {/* <Link to="/">Computer Networks</Link> */}
            </li>
            <li class="nav-item">
              <a class="nav-link" href="/dbms">
                DBMS
              </a>
              {/* <Link to="/">DBMS</Link> */}
            </li>
            <li class="nav-item">
              <a class="nav-link" href="/about">
                About
              </a>
              {/* <Link to="/">About</Link> */}
            </li>
          </ul>
        </div>
      </nav>
    </div>
  );
}

export default Nav;
