//Object of question and answers.
const linesMaker = (lines) => {
  let tag = "";

  lines.forEach((line) => {
    tag += `<div><span>&#8226; </span>${line}</div>`;
  });

  return tag;
};

const codeMaker = (code) => {
  return `<code>${code}</code>`;
};

const boldMaker = (line) => {
  return `<p class="font-weight-bold">${line}</p>`;
};
